.home-hero {
  height: calc(100vh - var(--header-height)) !important;
  overflow: clip;
}
.airpods-card:nth-child(2) {
  h5 {
    color: #ffc72c !important;
  }
}

.mountain-1,
.mountain-2 {
  /* transform: translateY(10%); */
}

@media (min-width: 776px) {
  .masked-background {
    background-image: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0) 0%,
      #fff 25%,
      #fff 75%,
      rgba(255, 255, 255, 0) 100%
    );
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }

  .masked-bottom {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #fff 24.91%);
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
}
@media (max-width: 776px) {
  .sticky-images {
  background: linear-gradient(180deg, #FFF 84.97%, rgba(255, 255, 255, 0.00) 100%) !important;
}
}